import React, { Component } from 'react';
import ReactTypingEffect from 'react-typing-effect';

//Layout css
import '../css/colors/cyan.css';

// Layouts
import Header from './Layouts/header.js';

// Shared
import About from './Shared/about';
import Services from './Shared/services';
import Projects from './Shared/projects';
import Testimonial from './Shared/testimonial';
import ContactUs from './Shared/contact-us';
 
import vid1 from '../videos/MP4/White-Keyboard.mp4';
import vid2 from '../videos/WEBM/White-Keyboard.webm';

// Modal Video 
import ModalVideo from 'react-modal-video'
import '../../node_modules/react-modal-video/scss/modal-video.scss';


class Index1 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }
    openModal () {
        this.setState({isOpen: true})
      }

    componentDidMount()
    {
      document.body.classList = "";
      document.getElementById('nav-bar').classList.add('navbar-white');
    }


    render() {
        return (
            <React.Fragment>
                <Header />
                <section className="section bg-home home-half" id="home" data-image-src="images/bg-home.jpg">
                    <div className="bg-overlay">
                        <div class="bg-video">
                            {/* <img src={img1} alt="logo-img" className="test-image" /> */}
                             <video class="test-image" autoPlay muted loop>
                                <source src={vid1} type="video/mp4" />
                                <source src={vid2}  type="video/webm" />
                            </video> 

                        </div>
                    </div>
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-8 offset-lg-2 text-white text-center">
                                <h4 className="home-small-title">Jon Hualde - Front end Developer</h4>
                                <h1 className="home-title">Fetch, Connect and Build</h1>
                                <ReactTypingEffect 
                                    className="padding-t-15 home-desc mx-auto" 
                                    text="Hi, I'm Jon. I'm a front end developer, specialized in React. 
                                    I will help you to build your next web application. 
                                    Have a look below to discover my work!"
                                    speed={50}
                                /> 
                                <ModalVideo channel='youtube' isOpen={this.state.isOpen} videoId='L61p2uyiMSo' onClose={() => this.setState({isOpen: false})} />
                            </div>
                        </div>
                    </div>
                </section>
                <About />
                <Projects />
                <Services />
                <Testimonial />
                <ContactUs />
            </React.Fragment>
        );
    }
}

export default Index1;


