
import React, { Component } from 'react';

import jon from '../../images/jon.png'; 

class About extends Component {

    render() {

        return (
            <React.Fragment>
                <section className="section" id="about">
                    <div className="container container-about">
                        <div className="row align-items-center">
                            <div className="col-lg-5 order-2 order-lg-1">
                                <div className="features-box mt-5 mt-lg-0">
                                    <h3 style={{ textAlign: 'center'}}>Who am I?  What can I bring to your team?</h3>
                                    <p className="text-muted web-desc">
                                        I was previously helping business owners and CEOs to develop their businesses, 
                                        by increasing their sales using online marketing and helping them 
                                        to choose the best softwares in the market to do so.
                                    </p>
                                    <p className="text-muted web-desc">
                                        After 6 years improving my sales and communication skills as a business developer, 
                                        I wanted to discover an area that attracted me for a long time: Programmation. 
                                        After some researches, I chose to start this new career as a front end developer. 
                                        Focusing on: 
                                    </p>
                                    <ul className="text-muted list-unstyled margin-t-30 features-item-list">
                                        <li>
                                            Using Javascript as my main language, with React library. As well as Redux for state management.
                                        </li>
                                        <li>
                                            Learn and use UI libraries (Ant design, Material UI, Tachyons...) 
                                            in order to provide the best design to my customers. 
                                        </li>
                                        <li>
                                            Stay tuned: I keep learning new features about React library, advanced Javascript and topics related to 
                                            front end development every day.
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-7 order-1 order-lg-2">
                                <div className="features-img mx-auto mr-lg-0">
                                    <img src={jon} alt="macbook" className="img-fluid" />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </React.Fragment>
        );
    }
}

export default About;


