
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import brain from '../../images/brain.jpg';
import natours from '../../images/natours.jpg';
import wog from '../../images/wog3.png';
import crownClothing from '../../images/crown-clothing4.png';

import Outdoors from '../outdoors-project.component.jsx'
import Smartbrain from '../smartbrain-project.component.jsx'
import UClothing from '../uclothing-project.component.jsx'
import WorkingOnTheGlobe from '../wog-project.component.jsx'

import ModalVideo from 'react-modal-video';

import '../../css/project.scss';

class Project extends Component {
    constructor(){
        super();
        
        this.state = {
            isOpen: false,
            outdoorsOpen: false,
            smartbrainOpen: false,
            uclothingOpen: false,
            wogOpen: false,
            id: '',
            Github: {
                UClothingRepoUrl: 'https://github.com/JonHualde/Uclothing',
                smartBrain: {
                    frontEndRepoUrl: 'https://github.com/JonHualde/SmartBrain',
                    backEndRepoUrl: 'https://github.com/JonHualde/Smartbrain-API'
                },
                outdoorsRepoUrl: 'https://github.com/JonHualde/Outdoors'
            },
            Website: {
                UClothingWebsite: 'https://uclothing.herokuapp.com/',
                smartbrainWebsite: 'https://smart-brain-jon.herokuapp.com/',
                workingOnTheGlobeWebsite: 'http://wog.mikecodeur.com/',
                outdoorsWebsite: 'https://jonhualde.github.io/Outdoors/'
            },
        }
    }

    goTo(link) {
        window.open(link)
    }

    openModal () {
        this.setState({ isOpen: true })
    }

    render() {
    const { Github, Website, outdoorsOpen, uclothingOpen, wogOpen, smartbrainOpen } = this.state;
    const { history } = this.props;

    return (
    <React.Fragment>
        <ModalVideo 
            channel='youtube' 
            isOpen={this.state.isOpen} 
            videoId={this.state.id} 
            onClose={ () => this.setState({ isOpen: false, id: '' }) }
        />
        <section className="section" id="projects">
            <div className="container">
                <div className="row">
                    <div className="col-lg-8 offset-lg-2">
                        <div className="about-title mx-auto text-center">
                            <h2 className="font-weight-light">
                                My projects
                            </h2>
                            <div className="section-title-border margin-t-20"></div>
                            <p className="text-muted pt-4">
                                <strong>
                                HOVER/CLICK on a project picture to access the video/Github/website.
                                </strong><br/>
                                Explanatory videos, repositories and websites available!
                            </p>
                        </div>
                    </div>
                </div>

                <div className="row margin-t-20">
                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                                
                                <div className="container-member">
                                    <img alt="project-image" src={crownClothing} 
                                         className="img-fluid rounded" 
                                         id="image-project-image"
                                    />
                                    <div id="image-project-caption">
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Github.UClothingRepoUrl)}
                                        >
                                            Github 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={ () => { this.setState({ id: 'RvFMrpLfoJA'}) ; this.openModal() }}    
                                        >
                                            Video 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Website.UClothingWebsite)}
                                        >
                                            Go to website 
                                        </figcaption> 
                                    </div>
                                </div>
                            </div>
                            <h4 className="team-name">
                                U-CLOTHING 
                            </h4>
                            <p className="text-uppercase team-designation">
                                Online clothing store, authentication + payment included
                            <br/>
                            <strong> 
                                MADE WITH React/Redux-Saga/Firebase/GraphQl
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Beta version available  &#00;
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p> 
                            {/* pe-7s-clock */}
                        </div>
                        <p id='fullDescription-text' 
                            onClick={ () => this.setState({ 
                                uclothingOpen: !this.state.uclothingOpen,
                                smartbrainOpen: false,
                                outdoorsOpen: false,
                                wogOpen: false })
                            }>
                            FULL DESCRIPTION HERE
                        </p>
                    </div>

                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                                <div className="container-member">
                                        <img alt="" src={brain} 
                                             className="img-fluid rounded" 
                                             id="image-project-image"
                                        />
                                        <div id="image-project-caption__smart">
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.smartBrain.frontEndRepoUrl)}
                                            >
                                                FrontEnd 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.smartBrain.backEndRepoUrl)}
                                            >
                                                BackEnd 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={ () => { this.setState({ id: 'joad962S6-M'}) ; this.openModal() }}
                                            >
                                                Video 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Website.smartbrainWebsite)}
                                            >
                                                Go to website 
                                            </figcaption> 
                                        </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                SMARTBRAIN
                            </h4>
                            <p className="text-uppercase team-designation">
                                Face recognition web application calling clarify API 
                            <br/>
                            <strong> 
                                Made with React/Node.js/PostgreSQL
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Available &#00;  
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p>
                        </div>
                        <p id='fullDescription-text' 
                            onClick={ () => this.setState({ 
                                smartbrainOpen: !this.state.smartbrainOpen,
                                outdoorsOpen: false,
                                uclothingOpen: false,
                                wogOpen: false })
                            }>
                             FULL DESCRIPTION HERE
                        </p>
                    </div>

                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                                <div className="container-member">
                                    <img alt="" src={wog} 
                                            className="img-fluid rounded" 
                                            id="image-project-image"
                                    /> 
                                    <div id="image-project-caption">
                                        <figcaption id="Link-text" 
                                        onClick={ () => { this.setState({ id: 'PtbFEXp9Tj4'}) ; this.openModal() }}>
                                            Video 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Website.workingOnTheGlobeWebsite)}
                                        >
                                            Go to website 
                                        </figcaption> 
                                    </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                WORKING ON THE GLOBE
                            </h4>
                            <p className="text-uppercase team-designation">
                                Video plateform for entrepreneurs and digital nomads
                                <br/>
                                <strong> 
                                    Made with React/Redux-saga/GraphQl
                                </strong>
                                <br/>
                                <strong style={{ color: 'green'}}>
                                    Available, project on hold &#00;
                                </strong>
                                <i className="pe-7s-like2 text-custom"></i>
                            </p>
                        </div>
                         <p id='fullDescription-text' 
                            onClick={ () => this.setState({ 
                                wogOpen: !this.state.wogOpen,
                                smartbrainOpen: false,
                                uclothingOpen: false,
                                outdoorsOpen: false })
                            }>
                            FULL DESCRIPTION HERE
                        </p>
                    </div>

                    <div className="col-lg-3 col-sm-6 projects_container">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                         
                                <div className="container-member">
                                        <img alt="" src={natours} 
                                             className="img-fluid rounded"
                                             id="image-project-image" 
                                        /> 
                                        <div id="image-project-caption">
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.outdoorsRepoUrl)}
                                            >
                                                Github 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={ () => { this.setState({ id: 'wm1oA_Bcrek'}) ; this.openModal() }}
                                            >
                                                Video 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Website.outdoorsWebsite)}
                                            >
                                                Go to website 
                                            </figcaption> 
                                        </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                OUTDOORS
                            </h4>
                            <p className="text-uppercase team-designation">
                                Designed for tours agencies with stunning effects, advanced CSS
                            <br/>
                            <strong> 
                                MADE WITH HTML/CSS with SASS/JAVASCRIPT
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Available &#00;
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p>
                        </div>
                        <p id='fullDescription-text' 
                            onClick={ () => this.setState({ 
                                outdoorsOpen: !this.state.outdoorsOpen,
                                smartbrainOpen: false,
                                uclothingOpen: false,
                                wogOpen: false })
                            }>
                            FULL DESCRIPTION HERE
                        </p>
                    </div>
                </div>
            </div>
            { !uclothingOpen ? null : <UClothing className='project_detail_transition'/> }
            { !smartbrainOpen ? null : <Smartbrain/> }
            { !wogOpen ? null : <WorkingOnTheGlobe/> }
            { !outdoorsOpen ? null : <Outdoors/> }
        </section> 
    </React.Fragment>
        );
    }
}

export default withRouter(Project);


